import { Stage, Text, Graphics, useTick } from "@inlet/react-pixi";
import { TextStyle } from "pixi.js";
import { useContext, useState, useEffect } from "react";
import styled, { css } from "styled-components";
import Styles from "../base/styles";
import SystemUtil from "../utils/systemUtil";
import { GlobalContext } from "./manage";
import Ticker from "./Ticker";

const Test = () => {

    const { store, setStore } = useContext(GlobalContext);

    const [interval, setInterval] = useState(-1);

    const start = (val: number) => {
        setInterval(val);
    }
    return (<_Wrap>
        {interval === -1 ? (<>
            <_Blank />
            <_StartDiv onClick={() => start(30)}>{'INTERVAL 30'}</_StartDiv>
            <_StartDiv onClick={() => start(20)}>{'INTERVAL 20'}</_StartDiv>
            <_StartDiv onClick={() => start(10)}>{'INTERVAL 10'}</_StartDiv>
        </>) : <>
            <Stage
                width={SystemUtil.CONTENTS_WIDTH}
                height={SystemUtil.CONTENTS_WIDTH}
                options={{
                    backgroundColor: 0x000011,
                    antialias: true
                }}
            ><Ticker interval={interval} /></Stage>
            <_StartDiv onClick={() => start(-1)}>{'RESET'}</_StartDiv>
        </>}
    </_Wrap>);
}

export default Test;

const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #535353;
`;

const _Blank = styled.div`
    display: inline-block;
    position: relative;
    width: ${SystemUtil.CONTENTS_WIDTH}px;
    height: ${SystemUtil.CONTENTS_WIDTH}px;
    background-color: #6d6d6d;
`;


const _StartDiv = styled.div`
    display: inline-block;
    position: relative;
    margin: 20px 0 0 50px;
    width: calc(100% - 100px);
    height: 100px;
    background-color: #8d8d8d;
    font-size: 68px;
    font-weight: 600;
    color: #fff;
    text-align: center;
    border: 3px solid #fff;
    box-sizing: border-box;
    border-radius: 10px;
`;
