import { Stage, Text, Graphics, useTick } from "@inlet/react-pixi";
import { TextStyle } from "pixi.js";
import { useContext, useState, useEffect } from "react";
import styled, { css } from "styled-components";
import Styles from "../base/styles";
import SystemUtil from "../utils/systemUtil";
import { GlobalContext } from "./manage";

type Particle = {
    x: number;
    y: number;
    r: number;
    l: number;
    color: string;
    timer: number;
}

type LocalState = {
    delta: number;
    frame: number;
    list: Particle[];
}
const Ticker = (props: {
    interval: number;
}) => {

    const [localState, setLocalState] = useState<LocalState>({
        delta: 1,
        frame: 0,
        list: []
    });
    const invalidate = () => setLocalState({ ...localState });

    const barrage = (arg: {
        x: number;
        y: number;
        roundDiv: number;
        circleNum: number;
        color: string;
    }) => {
        for (let i = 0; i < arg.circleNum; i++) {
            const noise = + Math.random() * 0.1;
            for (let j = 0; j < arg.roundDiv; j++) {
                localState.list.push({
                    x: arg.x,
                    y: arg.y,
                    r: 2 * 3.1415 / arg.roundDiv * j + noise,
                    l: 0.3 + i * 1.3,
                    color: arg.color,
                    timer: 20 + Math.floor(Math.random() * 70)
                });
            }
        }
    }
    useTick(delta => {
        localState.delta = delta;
        // console.log(Math.floor(frame / 60));

        if (localState.frame % props.interval === 0) {
            const x = Math.random() * SystemUtil.CONTENTS_WIDTH;
            const y = Math.random() * SystemUtil.CONTENTS_WIDTH;
            const roundDiv = 10 + Math.random() * 60;
            const circleNum = 3 + Math.random() * 15;
            const getColor = () => {
                switch (Math.floor(Math.random() * 5)) {
                    case 0: return '00ff00';
                    case 1: return 'ffff00';
                    case 2: return '00ffff';
                    case 3: return 'ff0000';
                    case 4: return 'ff00ff';
                }
                return 'ffffff';
            }
            barrage({
                x, y, roundDiv, circleNum, color: getColor()
            });
        }

        for (let i = localState.list.length - 1; i >= 0; i--) {
            const par = localState.list[i];
            if (par.timer === 0) {
                localState.list.splice(i, 1);
                continue;
            }
            const vecx = Math.cos(par.r) * par.l;
            const vecy = Math.sin(par.r) * par.l;
            par.x += vecx;
            par.y += vecy;
            if (par.l > 0.1) {
                par.l *= 0.95;
            }
            par.timer--;
        }

        localState.frame++;
        invalidate();
    });
    return (
        < >
            {/* <Text text="Hello World"
                x={100}
                y={100}
                rotation={frame/33}
                anchor={0.5}
                style={new TextStyle({ fill: 'white' })}
            /> */}
            <Graphics draw={g => {
                g.clear();

                localState.list.forEach(par => {

                    g.beginFill(Number(`0x${par.color}`));
                    g.drawCircle(par.x, par.y, 4);
                    g.endFill();
                });

                // g.beginFill(0xff0000);
                // g.drawRect(0, frame, 100, 100);
                // g.drawRect(111, 111, 100, 100);
                // g.drawCircle(100, 200 + frame, 3);
                // g.endFill();
            }} />
            <Text
                text={`num: ${localState.list.length}`}
                x={5}
                y={5}
                style={new TextStyle({ fill: 'white' })}
            />
            <Text
                text={`FPS: ${Math.floor(localState.delta * 60)}`}
                // text={`FPS: ${localState.delta}`}
                x={5}
                y={30}
                style={new TextStyle({ fill: 'white' })}
            />
        </>
    );
}

export default Ticker;